<template>
    <div class="w-full md:h-[50rem] h-96 relative">
        <div class="w-full h-full bg-gray-900 opacity-75 absolute z-20" :class="statusPlay && 'hidden'"/>
        <div class="w-full h-full absolute z-30">
            <div class="w-full h-full flex items-center" :class="statusPlay && 'hidden'">
                <svg class="w-full cursor-pointer" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="100" height="100">
                    <path fill="none" d="M0 0h24v24H0z"/>
                    <path @click="togglePlay" d="M19.376 12.416L8.777 19.482A.5.5 0 0 1 8 19.066V4.934a.5.5 0 0 1 .777-.416l10.599 7.066a.5.5 0 0 1 0 .832z" fill="#006EB5"/>
                    <path @click="togglePlay" d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zM10.622 8.415a.4.4 0 0 0-.622.332v6.506a.4.4 0 0 0 .622.332l4.879-3.252a.4.4 0 0 0 0-.666l-4.88-3.252z" fill="white" />
                </svg>
            </div>
        </div>
        <div class="h-full w-full" :class="statusPlay && 'z-30'"  ref="player" @click="togglePlay" />
    </div>
</template>


<script>
    import { usePlayer } from '@vue-youtube/core';
    import { ref } from 'vue';

    export default {
        name: 'YoutubeSection',
        components: {},
        setup() {
            const videoId = ref('n8d6n4z_us4');
            const player = ref();
            const statusPlay = ref(false);

            const { togglePlay, onStateChange } = usePlayer(videoId, player, {
                cookie: false,
                playerVars: {
                    mute: 2,
                },
            });

            onStateChange(() => {
                statusPlay.value = !statusPlay.value
            });

            return {
                player,
                togglePlay,
                statusPlay
            }
        }
    }
</script>